.form {
  text-align: right;
}

.form button {
  font: inherit;
  cursor: pointer;
  background-color: #0b4141;
  border: 1px solid #0b4141;
  color: #faf0dc;
  padding: 0.25rem 2rem;
  border-radius: 20px;
  font-weight: bold;
}

.form button:hover,
.form button:active {
  background-color: #353c96;
  border-color: #353c96;
}
